.battlefield{
  display: flex;
}
.battlefield td{
  border: 1px solid rgba(0, 100, 200, .3);
  border-radius: 3px;
  background: rgba(200, 200, 200, .5);
  user-select: none;
}
.battlefield .highlight-cell{
  border-color: black;
}
.numeration{
  display: flex;
  justify-content: space-around;
}
.numeration-v{
  flex-flow: column;
  margin-top: 26px;
}
.numeration span{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 26px;
  height: 26px;
}
